.sort-wrap {
  display: flex;
  justify-content: flex-end;
}
.sort {
  width: 20vw;
  font-size: 16px;
  border: 1px solid #dcdcdc;
  position: relative;
  &__value {
    display: flex;
    align-items: center;
    font-size: 16px;
    height: 35px;
    padding: 0 15px;
    cursor: pointer;
    line-height: 100%;
    background: #1f1f1f;
    &:after {
      content: url("../../icons/arrowdown.svg");
      position: absolute;
      right: 15px;
      top: 56%;
      transform: translateY(-50%);
    }
  }
  &__dropdown {
    background: #1f1f1f;
    position: absolute;
    width: 100%;
    top: 36px;
    left: 0;
    li {
      padding: 0 15px;
      cursor: pointer;
      transition: all ease-in 0.2s;
      display: flex;
      align-items: center;
      height: 35px;
      &:hover {
        color: #ffffff;
        background-color: #282828;
      }
    }
  }
  &__clear {
    position: relative;
    height: 20px;
    width: 20px;
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      div {
        background: #ffffff;
      }
    }
    div {
      width: 17px;
      height: 2px;
      background: #dcdcdc;
      position: absolute;
      transform-origin: left;
      transition: background-color 0.2s ease-in;
      &:first-child {
        transform: rotate(45deg);
        top: 3px;
        left: 4px;
      }
      &:last-child {
        transform: rotate(-45deg);
        bottom: 3px;
        left: 4px;
      }
    }
  }
}
